.App {
  /* text-align: center; */
  background-color: rgb(246,246, 246);
  width: 100%;
  padding-top: 50px;
  height: auto;
}

thead > tr > td{
  width:150px;
  height: 30px;
  text-align: center;
  font-weight: bold;
}
tbody > tr > td{
  width:150px;
  height: 30px;
  text-align: center;
 
}
tbody > tr{
  border-style: solid;
  border-width: 0 0  0.5px 0;
  border-color: rgb(243,243, 243);;
} 

thead > tr  {
  background-color: rgb(246,246, 246);
  height: 50px;
  width: 100%;
  color: rgb(42,52,52);  
}
table{
  border-collapse: collapse;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(246,246, 246); 
  margin-right: 200px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(70, 195, 95);  
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(70, 195, 95); 
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
